import { IScore } from "@softgames/firebase-leaderboard/dist/types";
import { TweenManager } from "js/framework/TweenManager/TweenManager";
import { Layout } from "js/layouts/LeaderboardPopup";
import { game } from "js/main";
import { WaitingScreen } from "./WaitingScreen";

interface IGameOverPopupConfig {
  parent: PIXI.Container;
  restart: () => unknown;
  tweenMgr: TweenManager;
  score: number;
  username: string;
}

export class LeaderboardPopup {
  private readonly tweenMgr: TweenManager;
  private layout: Layout;

  constructor({
    parent,
    restart,
    tweenMgr,
    score,
    username,
  }: IGameOverPopupConfig) {
    this.tweenMgr = tweenMgr;
    const userScore = {
      name: username,
      score: score,
      localUser: true,
    };

    const leaderboardData = [ // mock data
      { name: "Alice", score: 9000 },
      { name: "Bob", score: 8000 },
      { name: "Carol", score: 7000 },
      { name: "Dan", score: 6000 },
      { name: "Erin", score: 5000 },
    ]

    // this.submitScore({ parent, userScore })
      // .then((leaderboardData) => {
        this.layout = new Layout();
        parent.addChild(this.layout.root);

        this.populateLeaderboard(leaderboardData, userScore);
        this.layout.continueButton.once('pointerdown', async () => {
          await this.hidePopup();
          restart();
        });

        this.showPopup();
      // });
  }

  private async submitScore({
    parent,
    userScore,
  }: {
    parent: PIXI.Container,
    userScore: IScore;
  }) {
    const waitingScreen = new WaitingScreen(parent);

    await game.leaderboard.submitScore(userScore.name, userScore.score);
    const leaderboard = await game.leaderboard.getLeaderboard();

    waitingScreen.destroy();

    return leaderboard;
  }

  private populateLeaderboard(leaderboardData: IScore[], userScore: IScore) {
    const setupScoreRank = (index: number, data: IScore, posReplacer?: string) => {
      const scoreContainer = this.layout.root.getChildByName(`score_${index}`);
      scoreContainer.visible = false;

      if (!data) return;

      const color = data.localUser ? 0xFFFF00 : 0xFFFFFF;

      const pos: PIXI.Text = scoreContainer.getChildByName(`score_pos_${index}`);
      pos.tint = color;
      pos.text = posReplacer || `${index + 1}.`;

      const name: PIXI.Text = scoreContainer.getChildByName(`score_name_${index}`);
      name.tint = color;
      name.text = data.name;

      const points: PIXI.Text = scoreContainer.getChildByName(`score_points_${index}`);
      points.tint = color;
      points.text = `${data.score}`;

      scoreContainer.visible = true;
    };

    // Fill all ranks
    const usedData = Array(10).fill(null).map((_, index) => {
      const data = leaderboardData[index];
      setupScoreRank(index, data);

      return data;
    }).filter(Boolean);

    if (usedData.every(score => Boolean(score.localUser) === false)) {
      const lastIndex = Math.min(9, usedData.length);
      const userLeadData = leaderboardData.find(data => data.localUser);
      setupScoreRank(
        lastIndex,
        userLeadData || userScore,
        userLeadData ? `${leaderboardData.indexOf(userLeadData) + 1}.` : "-",
      );
    }
  }

  private showPopup() {
    this.tweenMgr.create(this.layout.root)
    .from({alpha: 0}, 300, this.tweenMgr.easing.Sinusoidal.Out, true);
  }

  private async hidePopup() {
    await this.tweenMgr.create(this.layout.root)
      .to({alpha: 0}, 400, this.tweenMgr.easing.Sinusoidal.Out, true)
      .asyncOnComplete();

    this.layout.root.destroy();
  }
}
